<template>
  <div class="goods container">
    <h1>首页</h1>
    <ul v-if="list" class="row">
      <li class="col-lg-4">
        <router-link to="user/index">
          <p class="count">{{ list.orderCount }}</p>
          <p class="desc">
            <i class="fa fa-user-o" aria-hidden="true"></i>
            用户总数
          </p>
        </router-link>
      </li>
      <li class="col-lg-4">
        <router-link to="product/index">
          <p class="count">{{ list.productCount }}</p>
          <p class="desc">
            <i class="fa fa-shopping-cart" aria-hidden="true"></i>
            商品总数
          </p>
        </router-link>
      </li>
      <li class="col-lg-4">
        <router-link to="order/index">
          <p class="count">{{ list.userCount }}</p>
          <p class="desc">
            <i class="fa fa-check-square-o" aria-hidden="true"></i>
            订单总数
          </p>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: "",
    };
  },
  methods: {
    data() {
      fetch("http://adminv2.happymmall.com/manage/statistic/base_count.do")
        .then((res) => res.json())
        .then((res) => {
          this.list = res.data;
        });
    },
  },
  mounted() {
    this.data();
  },
};
</script>

<style scoped lang="scss">
.goods {
  width: 100%;
  h1 {
    padding: 10px 0 20px;
    margin-bottom: 9px;
    font-weight: 500;
    font-size: 35px;
  }
  ul {
    width: 100%;
    display: flex;
    padding-left: 0 !important;
    justify-content: space-around;
    li {
      margin-bottom: 25px;
      &:first-child a {
        background-color: #f0ad4e;
      }
      &:nth-of-type(2) a {
        background-color: #5cb85c;
      }
      &:last-child a {
        background-color: #4cb1cf;
      }
      a {
        width: 100%;
        height: 160px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: #fff;
        transition: transform 0.2s;
        .count {
          font-size: 50px;
        }
        .desc {
          font-size: 18px;
          margin-top: 5px;
        }
        &:hover {
          transform: scale(1.1);
          color: #333;
        }
      }
    }
  }
}
.container {
  max-width: 100%;
  .row {
    margin: 0;
  }
}
</style>
